	module.exports = {"data":{"post":{"html":"<p>A couple days late again. Deutsche Telekom announced over the weekend they would be doubling (to 40 cents)  the price of sending SMS messages for new customers, then withdrew the plans Monday after a huge media and political backlash. The mean old magenta T would be taking advantage of all those teenagers. As if the cell providers don't already... one radio report mentioned offhand that SMS generates one-third of income from cellular networks.</p>","fields":{"title":"We Want The Airwaves For SMS","date":"24 Jan 2002","url":"https://papascott.de/2002-01-24-we-want-the-airwaves-for-sms/"}}},"pathContext":{"slug":"/2002-01-24-we-want-the-airwaves-for-sms/","prev":{"fields":{"title":"Schilly To Stay","date":"24 Jan 2002","slug":"/2002-01-24-schilly-to-stay/"}},"next":{"fields":{"title":"Experimental Failure","date":"25 Jan 2002","slug":"/2002-01-25-experimental-failure/"}}}}

